@import url('https://fonts.googleapis.com/css2?family=Open+Sans&display=swap');

html {
    scroll-behavior: smooth;
}

body {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: var(--main-font);
    overflow-x: hidden;
}

/* If editing css please use these variables */
:root {
    --main-font: 'Open Sans', sans-serif;
    --selector-color: #ff8528;


}


#linkToAccount>svg>path {
    stroke-width: 0;
}

.nav-content-right:hover {
    color: lightblue;
}

svg,
path {
    stroke-width: 1;
}


#dropdown-content {
    position: absolute;
    top: -250px;
    right: 0px;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: 0.2s;
    border: 1px solid gray;
    border-radius: 5px;
}

#dropdown-content>a {
    color: black;
    padding: 12px 5px;
    text-decoration: none;
    display: block;
    box-sizing: border-box;
}

#dropdown-content>a:hover {
    width: 100%;
    background-color: #f1f1f1;
}

#dropdown-content>a:last-child {
    color: red;
}


*::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 0.5px rgba(191, 191, 191, 0.3);
    border-radius: 10px;
    background-color: #F5F5F5;
}

*::-webkit-scrollbar {
    width: 12px;
    background-color: #F5F5F5;
}

*::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0);
    background-color: #d1d1d1;
}

/* Using these for now, going to change them to Images/Icons */
#logo,
#linkToSignIn,
#linkToAccount,
.nav-link {
    text-decoration: none;
    font-family: var(--main-font);
    color: black;
}

#logo {
    display: flex;
    align-items: center;
    width: 3em;
}

#logo>img {
    height: 2.5em;
    border-radius: 50%;
}

header {
    padding: 10px 0px 10px 0px;
}

.content-top {
    display: flex;
    flex-direction: column
}

.top-nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: white;
    height: 3em;
    border-bottom: 1px solid rgba(128, 128, 128, 0.473);
    z-index: 1;
}

.nav-content-left {
    margin: 0 10px;
}

.nav-content-right {
    margin: 0 10px;
    scale: 1.1;
    display: flex;

}

#logo {
    display: flex;
    justify-content: center;
    scale: 1.1;
}

#linkToSignIn {
    margin-right: 10px;
}

#welcome {
    margin-right: 10px;
    font-size: 1em;
}

#linkToSignIn:hover {
    color: #ff8528;
}

#linkToAccount {
    margin-right: 10px;
