    align-items: center;
    cursor: pointer;
}

.share-buttons {
    display: flex;
    align-items: center;
    justify-content: center;
}

.share-buttons>button {
    scale: 1.6;
    margin: 0 40px;
}

.share-buttons>button:nth-child(1)>svg {
    color: #3b5998;
}

.share-buttons>button:nth-child(2)>svg {
    color: #00acee;
}

.share-buttons>button:nth-child(3)>svg {
    color: #02d891;
}

.account-icon {
    font-size: 1.5em;
    color: black;
    cursor: pointer;
}

.profileImg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 5px;
}

.account-icon:hover {
    color: #ff8528;

}

.homepage-container {
    min-height: 100vh;
}

.header-container {
    height: 45px;
    background-color: white;
    position: relative;
}

#mobile-nav {
    position: absolute;
    top: 45px;
    width: 100%;
    height: 0vh;
    background-color: white;
    display: none;
    overflow: hidden;
    z-index: 1;
    transition: 0.2s;
}

.mobile-header-content {
    display: none;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.mobile-nav-open {
    display: flex;
    align-items: center;
    position: relative;
    left: 10px;
    flex-basis: 10%;
}

.nav-button {
    font-size: 1.5em;
    cursor: pointer;
}

.nav-button:hover {
    color: lightblue;
}

.mobile-header-content-middle {
    display: flex;
    align-items: center;
}

.text-logo {
    position: relative;
    left: 20px;
}

.text-logo>p {
    font-size: 1.6em;
    font-style: italic;
    font-family: var(--main-font);
    font-weight: 600;
    margin: 0;
    color: rgb(23, 23, 23)
}

.mobile-header-content-right {
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-basis: 20%;
}

.search-bar-icon-btn {
