    padding: 0;
    margin: 0;
    border: 1.5px;
    border-bottom-style: solid;
    border-bottom-color: rgb(211, 212, 213);
    padding-inline-start: 10px;
}

.homepage-hero {
    height: auto;
    position: relative;
}

.homepage-hero-images {
    display: flex;
}

.hero-image1 {
    width: 50%;
}

.hero-image2 {
    width: 50%;
    height: auto;
}


.homepage-hero-info {
    position: absolute;
    bottom: 30px;
    left: 5px;
}

.shop-now-button {
    cursor: pointer;
}

.shop-now-button-text {
    text-decoration: none;
    font-weight: 800;
    font-family: var(--main-font);
    font-size: 1em;
    background-color: white;
    padding: 10px 30px;
    border-radius: 2px;
    color: black;
}

.main-body {
    display: flex;
    flex-direction: column;
    margin-top: 30px;
}

.find-your-style-container {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
}

.find-your-style-container>h2 {
    display: block;
    font-weight: 600;
    width: auto;
    text-align: center;
    padding-bottom: 5px;
}

.find-your-style-boxes {
    display: flex;
    width: 100%;
    overflow-x: auto;
    scroll-behavior: smooth;
    scroll-snap-type: x mandatory;

}

.find-your-style-boxes>div {
    background-color: white;
    min-width: 300px;
    max-width: 300px;
    height: auto;
    margin: 0 10px 0px 10px;
