.search-btn {
    cursor: pointer;
}

#search-page {
    background-color: white;
    position: fixed;
    height: 0vh;
    width: 100vw;
    overflow-y: scroll;
    bottom: 0;
    transition: 0.2s;
    z-index: 9999;
}

.search-page-content {
    display: flex;
    flex-direction: column;
}

.search-page-content>.search {
    margin: auto;
    position: relative;
    left: 0;
    right: 0;
    display: flex;
    padding-bottom: 40px;
    width: 60%;
}



.form-container,
.login-form-container {
    display: block;
    margin-top: 50px;
    margin-bottom: 50px;
}

.register-page-container-left,
.login-page-container-left>h1 {
    text-align: center;
    margin: 0 0 0px 0;
}

.register-page-container,
.login-page-container {
    display: flex;
}

.register-page-container>div,
.login-page-container>div {
    width: 50%;
}

.register-page-container-left,
.login-page-container-left {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.name-form {
    display: flex;
    flex-direction: column;
    width: 90%;
    margin: 10px auto 10px auto;
}

.name-form>h2 {
    margin: 0 0 0 0;
}

.name-form>div {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.name-form>div>input {
    width: 100%;
    height: 50px;
    border: none;
    background-color: rgb(233, 233, 233);
    border-radius: 2px;
    margin: 10px auto 10px auto;
    padding: 0 10px 0 10px;
    box-sizing: border-box;
}

.name-form>div>input:focus {
    outline: 1px solid black;
}

.gender-form {
    width: 90%;
    margin: 10px auto 10px auto;
}

.gender-form-radio-buttons {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.gender-form-radio-buttons>div {
    display: flex;
    align-items: center;
}

.gender-label {
