    display: flex;
    align-items: center;
}

input[type="radio"] {
    width: 28px;
    height: 28px;
}

.registration-form,
.login-form {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 90%;
    margin: 10px auto 10px auto;
}

.registration-form>input,
.login-form>input {
    width: 100%;
    box-sizing: border-box;
    height: 50px;
    border: none;
    background-color: rgb(233, 233, 233);
    border-radius: 2px;
    margin: 10px auto 10px auto;
    padding: 0 10px 0 10px;
}

.registration-form>input:focus,
.login-form>input:focus {
    outline: 1px solid black;
}

.register-page-container-right,
.login-page-container-right {
    position: relative;
    background-image: url(../images/Not-Nike.png);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
}

.register-page-container-right {
    background-image: url(../images/account.jpg);
}

.to-login-link {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: none;
    background-color: rgb(25, 25, 25);
    border-radius: 2px;
    /* margin: 10px auto 10px auto; */
    padding: 20px 50px;
    color: white;
    font-weight: 800;
    font-family: var(--main-font);
    font-size: 1em;
    cursor: pointer;
    text-decoration: none;
}

.hidden-to-desktop {
    display: none;
}

.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.75);
    z-index: 0;
}

.login-overlay {
    position: absolute;
    top: 60%;
    font-size: 1.5em;
    z-index: 1;
    display: flex;
    flex-direction: column;
}

.login-overlay>h2 {
    color: white;
    font-size: 1.5em;
    font-weight: 800;
    font-style: oblique;
    margin: 0 0 0px 0;
}

.login-overlay>a {
    border: none;
    background-color: rgb(25, 25, 25);
    border-radius: 2px;
    /* margin: 10px auto 10px auto; */
    padding: 20px 50px;
    color: white;
    font-weight: 800;
    font-family: var(--main-font);
    font-size: 1em;
    cursor: pointer;
    text-decoration: none;
}

.register-page-container-right>.logo-grid {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    overflow: hidden;
}

.login-page-container-right>.logo-grid>.logo-grid-item>h1 {
    color: rgba(255, 0, 0, 0);
}

.register-page-container-right>.logo-grid>.logo-grid-item>h1 {
    color: rgba(255, 255, 255, 0.212);
    font-size: 3em;
    transform: rotate(45deg);
    font-weight: 800;
    font-style: oblique;
    margin: 0 0 0px 0;
    z-index: 1;
    text-shadow: 0 0 2px #f1ffb9, 0 0 2px #f1ffb9, 0 0 2px #f1ffb9, 0 0 2px #f1ffb9, 0 0 2px #f1ffb9, 0 0 2px #f1ffb9, 0 0 2px #f1ffb9;
}

.register-page-container-right>.logo-grid>.logo-grid-item>h1::before {
    content: "NOT NIKE";
    display: block;
    margin: 10px 0 0 0;
    color: yellow;
}

.register-page-container-right>.logo-grid>.logo-grid-item>h1::after {
    content: "NOT NIKE";
    display: block;
    margin: 10px 0 0 0;
    color: black;
}

.register-btn-container,
.login-btn-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 90%;
}

.register-btn,
.login-button {
    width: 100%;
    height: 50px;
    border: none;
    background-color: rgb(25, 25, 25);
    border-radius: 2px;
    padding: 0 10px 0 10px;
    color: white;
    font-family: var(--main-font);
    font-size: 1.3em;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* products  pages*/

.products-container {
    display: flex;
    min-height: 100vh;
    justify-content: center;
}

.products {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 1em;
    justify-content: space-between;
    min-height: 100%;
    flex-basis: 100%;
    margin: 0px 10px;
}

.products-shoe {
    max-width: 400px;
    padding-bottom: 20px;
    height: 576.8;
    background-color: transparent;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.products-shoe:hover {
    color: lightblue;
}

.product-image {
    border-top-right-radius: 5px;
    border-top-right-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.product-image img {
    max-width: 100%;
    height: 474px;

}


.product-info {
    padding: 20px;
}

.product-info p {
    margin: 0;
}

.product-info p:first-child {
    font-size: 1.2em;
    margin-bottom: 10px;
}


#filter-bar {
    min-height: 100%;
    flex-basis: 0%;
    width: 100%;
    position: relative;
    right: 100%;
    transition: 0.5s ease-in-out;
    position: relative;
    display: flex;
    flex-direction: column;
}

.filter {
    position: sticky;
    top: 0;
    height: 100vh;
    overflow: auto;
    display: flex;
    flex-direction: column;
}

.filter>div {
    margin-bottom: 10px;
    display: flex;
