/* Setting up device breakpoints */

/* Mobile */


@media only screen and (max-width: 600px) {

    .hero-image1 {
        width: 100%;
    }

    .hero-image2 {
        display: none;
    }

    .bottom-nav {
        display: none;
    }

    .mobile-header-content,
    #mobile-nav {
        display: flex;
    }

    .contrast-genres>div {
        min-width: 300px;
    }

    .contrast-genres>div>img {
        width: 100%;
        height: 100%;
        border-bottom-right-radius: 0;

    }

    .nav-link:hover {
        color: var(--selector-color);
    }

}

/* Large Mobile Devices+> */
@media only screen and (min-width: 600px) {
    .bottom-nav {
        display: flex;
        justify-content: space-around;
    }

    .bottom-nav-content-left {
        display: flex;
        justify-content: space-around;
        flex-basis: 30%;
    }

    .bottom-nav-content-left>.nav-links {
        width: 100%;
        display: flex;
        justify-content: flex-start;
        align-items: center;
    }

    .bottom-nav-content-left>.nav-links>a {
        text-decoration: none;
        color: black;
        font-size: 1rem;
        font-weight: 500;
        margin: 0 10px 0 10px;
    }

    .bottom-nav-content-left>.nav-links>a:hover {
        color: var(--selector-color);
    }

    .bottom-nav-content-middle {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-basis: 40%;
    }

    .bottom-nav-content-middle>div {
        font-size: 1.3em;
    }

    .bottom-nav-content-right {
        display: flex;
        justify-content: space-around;
        flex-basis: 30%;
        align-items: center;
    }

    .bottom-nav-content-right>div {
        width: 100%;
        display: flex;
        justify-content: flex-end;
    }

    #linkToContact {
        text-decoration: none;
        color: black;
        display: flex;
    }

    #linkToContact:hover {
        color: var(--selector-color);
    }

    .bottom-nav-content-right>div>div {
        display: flex;
        align-items: center;
        font-size: 1.8em;
        margin: 0 25px 0 25px;
    }

    .bottom-nav-content-right>div>div>*:hover {
        color: var(--selector-color);
    }

    #cart {
        display: flex;
        text-decoration: none;
        position: relative;
    }

    #cart p {
        position: absolute;
        font-size: 0.7em;
        top: -35px;
        left: 25px;
        color: black;
        font-weight: 100;
    }



    #dCart:hover>path {
        stroke: var(--selector-color);
    }

    .find-your-style-boxes {
        display: flex;
        flex-direction: row;
    }

}

@media only screen and (min-width: 960px) {
    .find-your-style-boxes {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
    }

    .find-your-style-boxes>div {
        max-width: 400px;
    }
}

@media only screen and (max-width: 768px) {
    #filter-bar {
        flex-basis: 70%;
        right: 0%;
    }

    .products {
        display: flex;
        flex-direction: column;
    }

    .functions-left {
        display: none;
    }

    .functions-right {
        flex-basis: 100%;
    }

    .products-shoe {
        width: fit-content;
    }

    .products-shoe img {
        height: auto;
    }

    .filter-button {
        display: none;
    }
}

@media only screen and (max-width: 1000px) {
    .shoe-page-container {
        display: flex;
        flex-direction: column;
    }

    .cart-page-container {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    .footer-content {
        display: flex;
        flex-direction: column;
    }

    .cart-item-info-name {
        display: flex;
        flex-direction: column;
    }

    .cart-item-sizeQuantity {
        display: flex;
        flex-direction: column;
    }
}

@media only screen and (max-width: 1308px) {

    .login-page-container,
    .register-page-container {
        display: flex;
        flex-direction: column;
    }

    .register-page-container>div,
    .login-page-container>div {
        width: 100%;
    }

    .login-page-container-right,
    .register-page-container-right {
        display: none;
    }

    .hidden-to-desktop {
        display: block;
    }


}